<template>
	<div class="commodity">
		<div class="commodity_title">
			<h4>{{arr.title}} <span>{{arr.text}}</span><a href="javascript:;">MORE+</a></h4>
		</div>
		<div class="commodity_box">
			<div class="big_img">
				<img :src="arr.img" alt="">
			</div>
			<div class="com">
				<ul>
					<li v-for="i in arr.children" @click="get_date(i)">
						<router-link to="/shop">
							<div class="com_img">
								<img :src="i.img" alt="">
							</div>
							<h6>{{i.title}}</h6>
							<p>{{i.text}}</p>
							<span>¥{{i.price}}</span>
						</router-link>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	import {mapActions} from 'vuex';
	export default {
		props: ['arr'],
		methods: mapActions(['get_date'])
	}
</script>

<style>
	.commodity{
		padding: 20px 80px;
		display: flex;
		flex-wrap: wrap;
	}
	.commodity_title{
		width: 100%;
		height: auto;
		margin-bottom: 20px;
	}
	.commodity_title h4{
		font-family: '微软雅黑';
		font-size: 22px;
		color: #333333;
		padding-left: 10px;
		vertical-align: bottom;
		border-left: 4px solid #dd3333;
		font-weight: 400;
	}
	.commodity_title h4 span{
		font-family: '微软雅黑';
		font-size: 13px;
		color: #a5a5a5;
		margin-left: 10px;
	}
	.commodity_title h4 a{
		font-family: 'century gothic';
		font-size: 10px;
		color: #666666;
		float: right;
	}
	.commodity_box{
		width: 100%;
		height: auto;
		display: flex;
		flex-wrap: wrap;
	}
	.big_img{
		width: 297px;
		margin-right: 20px;
		border: 1px #d5d5d5 solid;
	}
	.big_img img{
		width: 100%;
		height: 100%;
	}
	.com{
		width: calc(100% - 317px);
	}
	.com ul{
		height: 100%;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-content: space-between;
	}
	.com ul li{
		width: 31.5%;
		background: #fff;
		text-align: center;
		padding: 10px 0 32px;
		margin-top: 25px;
		font-family: '微软雅黑';
		transition: all .5s;
		cursor: pointer;
		position: relative;
	}
	.com ul li:nth-child(1),.com ul li:nth-child(2),.com ul li:nth-child(3){
		margin-top: 0;
	}
	.com_img{
		width: 100%;
		height: 70%;
	}
	.com_img img{
		width: 70%;
	}
	.com ul li h6{
		font-size: 15px;
		color: #191919;
		line-height: 21px;
		font-weight: 500;
		margin-top: 10px;
	}
	.com ul li p{
		font-size: 12px;
		color: #a0a0a0;
		margin: 10px 0 20px;
	}
	.com ul li span{
		color: #dd3333;
	}
	.com ul li:hover{
		transform: translateY(-10px);
		box-shadow: 0 1px 3px 3px #e5e5e5;
	}
	
	@media only screen and (min-width: 764px) and (max-width: 992px){
		.commodity{
			padding: 40px 25px;
		}
		.big_img{
			display: none;
		}
		.com{
			width: 100%;
			padding: 0 10px;
		}
		.com ul li{
			width: 49%;
		}
		.com ul li{
			margin-top: 20px !important;
		}
		.com ul li:hover{
			transform: translateY(0px);
			box-shadow: 0 0px 3px 3px #fff;
		}
	}
	
	@media only screen and (min-width: 0) and (max-width: 764px){
		.commodity{
			padding: 50px 10px;
		}
		.commodity_title h4{
			font-size: 17px;
		}
		.commodity_title h4 span{
			font-size: 13px;
		}
		.commodity_title h4 a{
			font-size: 12px;
			line-height: 26px;
		}
		.big_img{
			display: none;
		}
		.com{
			width: 100%;
			padding: 0 10px;
		}
		.com ul li{
			width: 48%;
		}
		.com ul li{
			margin-top: 20px !important;
		}
		.com ul li:hover{
			transform: translateY(0px);
			box-shadow: 0 0px 3px 3px #fff;
		}
	}
	
</style>



