<template>
	<div>
		<div class="crumbs">
			<router-link to="/">首页</router-link><span>/</span>
			{{name}}
		</div>
		<div class="site">
			<div class="map_box">
				<maps></maps>
			</div>
			<div class="posi">
				<h4>
					门店坐标<br>
					<span>CONTACT US</span>
				</h4>
				<div class="iconfont icon-shuangjiantou ic"></div>
				<ul>
					<li>地址：江苏省苏州工业园区金鸡湖大道00号</li>
					<li>电话：400 6666 6666</li>
				</ul>
				<div class="posi_from">
					<p>您也可以通过表单联系我们：</p>
					<input type="text" placeholder="姓名">
					<input type="text" placeholder="电话">
					<textarea placeholder="内容"></textarea><br>
					<button>发送</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import maps from './map.vue';
	export default {
		components: {
			maps
		},
		data() {
			return {
				name: '线下门店'
			};
		}
	}
</script>

<style>
	.site{
		padding: 20px 80px 80px;
		background: #fff;
		display: flex;
		flex-wrap: wrap;
	}
	.map_box{
		width: 65%;
		height: 500px;
		padding: 10px 0;
	}
	.posi{
		width: calc(32% - 100px);
		height: auto;
		padding: 20px 10px;
		margin-left: 100px;
		margin-top: 10px;
		border: 1px solid rgba(224,224,224,1);
	}
	.posi h4{
		font-size: 18px;
		text-align: center;
		color: #000;
		line-height: 24px;
	}
	.posi h4 span{
		color: #696969;
		font-size: 12px;
		font-weight: 400;
	}
	.ic{
		text-align: center;
		margin: 20px auto;
	}
	.posi ul{
		width: 100%;
		height: auto;
	}
	.posi ul li{
		width: 100%;
		font-size: 13px;
		color: #000;
		line-height: 25px;
	}
	.posi_from{
		width: 100%;
		height: auto;
		margin-top: 40px;
	}
	.posi_from p{
		font-size: 13px;
	}
	.posi_from input{
		width: 100%;
		height: 40px;
		background: #f2f2f2;
		border: none;
		padding-left: 5px;
		margin: 10px 0;
	}
	.posi_from textarea{
		width: 100%;
		height: 60px;
		padding: 5px;
		background: #f2f2f2;
		border: none;
	}
	.posi_from button{
		width: 110px;
		height: 32px;
		background: #000;
		color: #fff;
		text-align: center;
		border: none;
		margin-top: 20px;
	}
	
	@media only screen and (min-width: 764px) and (max-width: 992px){
		.map_box,.site{
			width: 100%;
		}
		.map_box{
			padding-bottom: 0;
			margin-bottom: 0;
		}
		.posi{
			width: 100%;
			margin: 0;
		}
	}
	
	@media only screen and (min-width: 0px) and (max-width: 764px){
		.map_box,.site{
			width: 100%;
		}
		.site{
			padding: 20px 10px;
		}
		.map_box{
			height: 200px !important;
			padding-bottom: 0;
			margin-bottom: 0;
		}
		.posi{
			width: 100%;
			margin: 0;
		}
	}
	
	
</style>



