<template>
	<div class="logon">
		<div class="logon_left">
			<h6>用户注册/登录说明：</h6>
			<ol>
				<li>1、演示用户名：<span>demouser</span>；密码： <span>demo1234</span></li>
				<li>2、实际使用之前，需配置用户管理组件中的相关邮件。详情请参考教程：《用户管理中相关邮件的配置》</li>
				<li>3、新用户注册后的审核方式有三种：自动核准、邮件激活、需要管理员批准（即人工审核），详情可参考教程 <span>《如何设置用户注册后的审核方式》</span></li>
			</ol>
		</div>
		<div class="logon_right">
			<div class="from">
				<table>
					<tr>
						<td style="width: 150px;"><span>电话号码<strong>*</strong></span></td>
						<td><input type="text"></td>
					</tr>
					<tr>
						<td style="width: 150px;"><span>短信验证码<strong>*</strong></span></td>
						<td><input type="text"></td>
					</tr>
					<tr>
						<td style="width: 150px;"><span>密码<a href="javascript:;" class="iconfont icon-icon-tishixinxi"></a><strong>*</strong></span></td>
						<td><input type="text"></td>
					</tr>
					<tr>
						<td style="width: 150px;"><span>再输一次密码</span></td>
						<td><input type="text"></td>
					</tr>
					<tr class="q">
						<td style="width: 150px;"></td>
						<td>
							密码强度
							<div></div>
						</td>
						
					</tr>
				</table>
				<div class="lo">
					<button>注册</button>
					<button><router-link to="/logon">已经有账号了？请登录</router-link></button><br>
					<p>第三方账号登录：<span class="iconfont icon-QQ"></span></p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				
			};
		}
	}
</script>

<style>
	.logon{
		width: 100%;
		height: auto;
		padding: 100px 80px;
		border-top: 2px solid rgba(221,51,51,1);
		display: flex;
		flex-wrap: wrap;
	}
	.logon_left{
		width: 50%;
		height: auto;
	}
	.logon_left h6{
		font-size: 15px;
		color: rgb(102, 102, 102);
		font-family: '微软雅黑';
		font-weight: 700;
	}
	.logon_left ol{
		width: 100%;
		color: rgb(102, 102, 102);
		font-size: 13px;
		margin: 50px 0;
	}
	.logon_left ol li{
		line-height: 30px;
	}
	.logon_left ol li span{
		color: #000;
	}
	.logon_right{
		width: 50%;
	}
	.from{
		width: 478px;
		height: auto;
		border: 1px solid #eee;
		border-radius: 5px;
		margin: 0 auto;
		padding: 20px;
	}
	.from table{
		width: 100%;
		height: auto;
		border-bottom: 1px solid #e5e5e5;
		padding-bottom: 20px;
	}
	.from table tr{
		line-height: 40px;
	}
	.from table tr td span{
		font-size: 12px;
		color: #333;
	}
	.from table tr td input{
		width: 100%;
		height: 30px;
		box-shadow: inset 0 -1px 4px #eee!important;
		background: #f5f5f5;
		border-radius: 3px;
		border: none;
		padding-left: 10px;
	}
	.from table tr td strong{
		margin-left: 10px;
		color: red;
		font-weight: 400;
	}
	.from table tr td a{
		color: #666;
		margin-left: 10px;
	}
	.q{
		font-size: 12px;
		color: #333;
	}
	.q div{
		width: 200px;
		height: 6px;
		background: #dee5ec;
	}
	.lo{
		padding: 10px 0 20px;
	}
	.lo button{
		width: 48px;
		height: 30px;
		border: none;
		background: #fff;
		border-radius: 3px;
	}
	.lo button:nth-of-type(1){
		background: #000;
		color: #fff;
		margin-right: 10px;
	}
	.lo button:nth-of-type(2){
		width: auto;
		padding: 0 10px;
		float: right;
		background: #fff;
		color: #000;
		border: 1px solid #ddd;
	}
	.lo button:nth-of-type(2) a{
		color: #000;
	}
	.lo p{
		font-size: 12px;
		color: #888;
		margin-top: 20px;
	}
	.lo p span{
		display: inline-block;
		width: 24px;
		height: 24px;
		border-radius: 50%;
		border: 1px #000 solid;
		text-align: center;
		line-height: 22px;
		color: #000;
		cursor: pointer;
	}
	
	
	
	@media only screen and (min-width: 764px) and (max-width: 992px){
		.logon{
			padding: 80px 100px 20px;
		}
		.logon_left,.logon_right{
			width: 100%;
		}
		.logon_right{
			margin: 20px auto;
		}
	}
	
	@media only screen and (min-width: 0px) and (max-width: 764px){
		.logon{
			padding: 80px 15px 20px;
		}
		.logon_left{
			padding: 0 15px;
		}
		.logon_left,.logon_right{
			width: 100%;
		}
		.logon_right{
			margin: 20px auto;
		}
		.from{
			margin: 0;
			width: 100%;
		}
	}
	
</style>



