<template>
	<div>
		<div class="kong" v-if="!get_type">
			<p>您的购物车目前是空的</p>
			<router-link to="/all">返回商店</router-link>
		</div>
		<div class="shopping" v-if="get_type">
			<div class="max_shopping_left">
				<table cellspacing="0">
					<tr>
						<th colspan="3" style="border-bottom: 2px #ddd solid;">商品</th>
						<th style="border-bottom: 2px #ddd solid;">价格</th>
						<th style="padding-left: 35px;border-bottom: 2px #ddd solid;">数量</th>
						<th style="border-bottom: 2px #ddd solid;">合计</th>
					</tr>
					<tr v-for="i in get_shopping_arr">
						<td><span @click="get_delete(i.uid)">×</span></td>
						<td><img :src="i.img" alt=""></td>
						<td>{{i.title}}</td>
						<td>¥{{i.price}}</td>
						<td><button @click="get_less(i.uid)">-</button><input type="text" :value="i.num"><button @click="get_plus(i.uid)">+</button></td>
						<td>¥{{i.num*i.price}}</td>
					</tr>
				</table>
			</div>
			<div class="min_shopping_left">
				<table cellspacing="0" cellpadding="0">
					<tr>
						<th colspan="3" style="border-bottom: 2px #ddd solid;">商品</th>
						<th style="border-bottom: 2px #ddd solid;">价格</th>
						<th style="padding-left: 35px;border-bottom: 2px #ddd solid;">数量</th>
						<th style="border-bottom: 2px #ddd solid;">合计</th>
					</tr>
					<tr v-for="i in get_shopping_arr">
						<td><span @click="get_delete(i.uid)">×</span></td>
						<td colspan="2">{{i.title}} <br><img :src="i.img" alt=""></td>
						<td>¥{{i.price}}</td>
						<td colspan="2"><button @click="get_less(i.uid)">-</button><input type="text" :value="i.num"><button @click="get_plus(i.uid)">+</button></td>
					</tr>
				</table>
			</div>
			<div class="shopping_right">
				<h4>购物车总计</h4>
				<p class="xj">购物车小计 <span>¥{{get_arr_price}}</span></p>
				<p class="yf">运费 <span>免费送运</span></p>
				<p class="zj">订单总计 <span>¥{{get_arr_price}}</span></p>
				<a href="javascript:;" class="syt">前往收银台</a>
				<h4>优惠券</h4>
				<a href="javascript:;" class="dm">优惠券代码</a>
				<a href="javascript:;" class="sy">使用优惠券</a>
			</div>
		</div>
	</div>
</template>

<script>
	import {mapGetters,mapActions} from 'vuex';
	export default {
		computed: mapGetters(['get_shopping_arr','get_arr_price','get_type']),
		methods: mapActions(['get_plus','get_less','get_delete']),
		data() {
			return {
				
			};
		}
	}
</script>

<style>
	.shopping{
		width: 100%;
		height: auto;
		display: flex;
		flex-wrap: wrap;
		padding: 30px 80px 100px;
		border-top: 1px dotted #ddd;
	}
	.max_shopping_left{
		width: 70%;
		height: auto;
	}
	.max_shopping_left table{
		width: 100%;
		height: auto;
		border-right: 1px #ddd solid;
		padding-right: 50px;
	}
	.max_shopping_left table tr th{
		font-size: 16px;
		color: #555;
		text-align: left;
		padding: 4px 12px;
	}
	.max_shopping_left table tr:nth-of-type(2) td{
		border-top: none;
	}
	.max_shopping_left table tr td{
		border-top: 1px #ddd dotted;
	}
	.max_shopping_left table tr td{
		padding-top: 10px !important;
		padding-bottom: 10px !important;
	}
	.max_shopping_left table tr td:nth-of-type(1){
		width: 61px;
		font-size: 16px;
		text-align: center;
	}
	.max_shopping_left table tr td:nth-of-type(1) span{
		display: inline-block;
		width: 23px;
		height: 23px;
		line-height: 21px;
		text-align: center;
		font-size: 16px;
		cursor: pointer;
		border-radius: 50%;
		border: 1px #ccc solid;
	}
	.max_shopping_left table tr td:nth-of-type(2){
		width: 120px;
		height: auto;
		padding: 0 20px;
	}
	.max_shopping_left img{
		width: 100%;
	}
	.max_shopping_left table tr td:nth-of-type(3){
		padding: 0 30px;
		font-size: 14px;
	}
	.max_shopping_left table tr td:nth-of-type(4){
		padding: 0 10px;
		color: #777;
		font-size: 14px;
	}
	.max_shopping_left table tr td:nth-of-type(5){
		padding: 0 10px;
	}
	.max_shopping_left table tr td:nth-of-type(5) button{
		width: 26px;
		height: 55px;
		border: 1px #cccccc solid;
		background: #fff;
	}
	.max_shopping_left table tr td:nth-of-type(5) input{
		width: 26px;
		height: 55px;
		text-align: center;
		background: #fff;
		border: none;
		border-top: 1px #cccccc solid;
		border-bottom: 1px #cccccc solid;		
	}
	.max_shopping_left table tr td:nth-of-type(6){
		font-size: 14px;
		color: #777;
		font-weight: bold;
	}
	.min_shopping_left{
		width: 70%;
		height: auto;
		display: none;
	}
	.min_shopping_left table{
		width: 100%;
		height: auto;
		border-right: 1px #ddd solid;
	}
	.min_shopping_left table tr th{
		font-size: 16px;
		color: #555;
		text-align: left;
		padding: 4px 12px;
	}
	.min_shopping_left table tr:nth-of-type(2) td{
		border-top: none;
	}
	.min_shopping_left table tr td{
		border-top: 1px #ddd dotted;
		position: relative;
	}
	.min_shopping_left table tr td:nth-of-type(1){
		width: 61px;
		font-size: 16px;
		text-align: center;
	}
	.min_shopping_left table tr td:nth-of-type(1) span{
		display: inline-block;
		width: 23px;
		height: 23px;
		line-height: 21px;
		text-align: center;
		font-size: 16px;
		cursor: pointer;
		border-radius: 50%;
		border: 1px #ccc solid;
	}
	.min_shopping_left table tr td:nth-of-type(2){
		height: auto;
		padding: 0 20px;
		font-size: 14px;
	}
	.min_shopping_left img{
		max-width: 90px;
	}
	.min_shopping_left table tr td:nth-of-type(3){
		padding: 0 10px;
		color: #777;
		font-size: 14px;
	}
	.min_shopping_left table tr td:nth-of-type(4){
		padding: 0 10px;
	}
	.min_shopping_left table tr td:nth-of-type(4) button{
		width: 26px;
		height: 28px;
		border: 1px #cccccc solid;
		background: #fff;
	}
	.min_shopping_left table tr td:nth-of-type(4) input{
		width: 26px;
		height: 28px;
		text-align: center;
		background: #fff;
		border: none;
		border-top: 1px #cccccc solid;
		border-bottom: 1px #cccccc solid;		
	}
	.min_shopping_left table tr td:nth-of-type(5){
		font-size: 14px;
		color: #777;
		font-weight: bold;
	}
	
	.shopping_right{
		width: 30%;
		height: auto;
		padding: 0 30px;
	}
	.shopping_right h4{
		font-size: 16px;
		color: #555;
		padding: 5px 0;
		border-bottom: 3px #ddd solid;
	}
	.shopping_right p{
		font-size: 14px;
		color: #777;
		font-weight: bold;
		margin: 10px 0;
	}
	.shopping_right p span{
		float: right;
		font-weight: 400;
	}
	.yf{
		border-bottom: 1px #ddd solid;
		padding-bottom: 10px;
	}
	.zj{
		border-bottom: 2px #ddd solid;
		padding-bottom: 10px;
	}
	.syt{
		display: block;
		text-align: center;
		background: #777777;
		color: #fff;
		width: 100%;
		height: 42px;
		line-height: 42px;
		margin: 10px 0 20px;
	}
	.dm,.sy{
		display: block;
		margin: 10px 0;
		width: 100%;
		height: 33px;
		line-height: 33px;
		border: 1px #777777 solid;
		padding-left: 10px;
		color: #919191;
		font-size: 14px;
	}
	.sy{
		text-align: center;
	}
	.kong{
		width: 100%;
		height: auto;
		padding: 100px 80px;
		text-align: center;
	}
	.kong p{
		font-size: 14px;
		color: rgb(119, 119, 119);
	}
	.kong a{
		display: inline-block;
		width: 105px;
		height: 42px;
		text-align: center;
		border: 1px #777 solid;
		color: #777;
		line-height: 40px;
		margin: 20px;
	}
	
	@media only screen and (min-width: 764px) and (max-width: 992px){
		.shopping{
			padding: 50px 10px;
		}
		.max_shopping_left{
			display: none;
		}
		.min_shopping_left{
			display: block;
		}
		.max_shopping_left,.min_shopping_left,.shopping_right{
			width: 100%;
		}
		.max_shopping_left table,.min_shopping_left table{
			border-right: none;
		}
		.max_shopping_left table tr:nth-of-type(1),.min_shopping_left table tr:nth-of-type(1){
			display: none;
		}
		.shopping_right{
			padding: 0;
		}
		.min_shopping_left table td{
			margin: 0 !important;
			padding: 0 !important;
		}
		.min_shopping_left img{
			max-width: 60px;
		}
	}
	
	@media only screen and (min-width: 0px) and (max-width: 764px){
		.shopping{
			padding: 50px 10px;
		}
		.max_shopping_left{
			display: none;
		}
		.min_shopping_left{
			display: block;
		}
		.max_shopping_left,.min_shopping_left,.shopping_right{
			width: 100%;
		}
		.max_shopping_left table,.min_shopping_left table{
			border-right: none;
		}
		.max_shopping_left table tr:nth-of-type(1),.min_shopping_left table tr:nth-of-type(1){
			display: none;
		}
		.shopping_right{
			padding: 0;
		}
		.min_shopping_left table td{
			font-size: 12px !important;
			margin: 0 !important;
			padding: 0 !important;
			/* text-align: center; */
		}
		.min_shopping_left img{
			max-width: 50px;
		}
	}

</style>



