<template>
	<div class="son" v-if="get_data_arr.length">
		<div class="crumbs">
			<router-link to="/">首页</router-link><span>/</span>
			{{get_data_arr[0]}}
		</div>
		<div class="son_son" v-for="i in get_data_arr[1]" v-if="get_data_arr[0] == i.type">
			<div class="son_title">
				<h4>{{i.title}} <span>{{i.text}}</span><a href="javascript:;">MORE+</a></h4>
			</div>
			<div class="son_data">
				<ul>
					<li v-for="j in i.children" @click="get_date(j)">
						<router-link to="/shop">
							<img :src="j.img" alt="">
							<h6>{{j.title}}</h6>
							<p>{{j.text}}</p>
							<span>¥{{j.price}}</span>
						</router-link>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	import {mapGetters} from 'vuex';
	import {mapActions} from 'vuex';
	export default {
		computed: mapGetters(['get_data_arr']),
		methods: mapActions(['get_date']),
		data() {
			return {
				
			};
		}
	}
</script>

<style>
	.son{
		width: 100%;
		background: #f2f2f2;
	}
	.son_title{
		width: 100%;
		height: auto;
		margin-bottom: 20px;
	}
	.son_son{
		width: 100%;
		height: auto;
		padding: 50px 80px;
	}
	.son_title h4{
		font-family: '微软雅黑';
		font-size: 22px;
		color: #333333;
		padding-left: 10px;
		vertical-align: bottom;
		border-left: 4px solid #dd3333;
		font-weight: 400;
	}
	.son_title h4 span{
		font-family: '微软雅黑';
		font-size: 13px;
		color: #a5a5a5;
		margin-left: 10px;
	}
	.son_title h4 a{
		font-family: 'century gothic';
		font-size: 10px;
		color: #666666;
		float: right;
	}
	.son_data{
		width: 100%;
		height: auto;
		display: flex;
		flex-wrap: wrap;
	}
	.son_data ul{
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-content: space-between;
		padding-top: 30px;
	}
	.son_data ul li{
		width: 31.5%;
		background: #fff;
		text-align: center;
		padding: 10px 0 32px;
		font-family: '微软雅黑';
		transition: all .5s;
		margin-bottom: 35px;
	}
	.son_data ul li:nth-child(1),.son_data ul li:nth-child(2),son_data ul li:nth-child(3){
		margin-top: 0;
	}
	.son_data ul li img{
		width: 70%;
	}
	.son_data ul li h6{
		font-size: 15px;
		color: #191919;
		line-height: 21px;
		font-weight: 500;
		margin-top: 10px;
	}
	.son_data ul li p{
		font-size: 12px;
		color: #a0a0a0;
		margin: 10px 0 20px;
	}
	.son_data ul li span{
		color: #dd3333;
	}
	
	.son_data ul li:hover{
		transform: translateY(-10px);
		box-shadow: 0 1px 3px 3px #e5e5e5;
	}
</style>



