<template>
	<div class="map" id="map">
	</div>
</template>

<script>
	export default{
		mounted(){
			var map = new BMap.Map("map");
			var point = new BMap.Point(116.404, 39.915);
			map.centerAndZoom(point, 12);
		}
	}
</script>

<style>
	.map{
		width: 100%;
		height: 100%;
	}
</style>



