import Vue from 'vue';
import Router from 'vue-router';
import Index from './../components/index/index.vue';
import login from './../components/login/login.vue';
import logon from './../components/login/logon.vue';
import all from './../components/all/all.vue';
import dynamic from './../components/dynamic/dynamic.vue';
import aboutUs from './../components/aboutUs/aboutUs.vue';
import site from './../components/site/site.vue';
import shop from './../components/shop.vue';
import shopping from './../components/shopping.vue';
import son from './../components/index/son.vue';

Vue.use(Router);

var routes = [
	{
		path: '/',
		name: 'index',
		component: Index
	},
	{
		path: '/login',
		name: 'login',
		component: login
	},
	{
		path: '/logon',
		name: 'logon',
		component: logon
	},
	{
		path: '/all',
		name: 'all',
		component: all
	},
	{
		path: '/dynamic',
		name: 'dynamic',
		component: dynamic
	},
	{
		path: '/aboutUs',
		name: 'aboutUs',
		component: aboutUs
	},
	{
		path: '/site',
		name: 'site',
		component: site
	},
	{
		path: '/shop',
		name: 'shop',
		component: shop
	},
	{
		path: '/shopping',
		name: 'shopping',
		component: shopping
	},
	{
		path: '/son',
		name: 'son',
		component: son
	}
];

var router = new Router({
	routes
})

export default router;



