<template>
	<div class="header">
		<!-- 大屏头部 -->
		<div class="max_header">
			<div class="max_header_top">
				<div class="max_header_data">
					<span class="iconfont icon-time"></span>
					 在线时间 08:30~21:30
					<span>欢迎访问家用设备在线商城！</span>
				</div>
				<div class="login">
					<span><router-link to="/logon">登录</router-link></span>
					<span><router-link to="/login">注册</router-link></span>
					<router-link to="/shopping">
						<span class="iconfont icon-30"></span>
					</router-link>
				</div>
			</div>
			<!-- 大屏导航 -->
			<div class="max_nav_box">
				<div class="max_logo">
					<img src="https://ccdn.goodq.top/caches/1a46466ba86176679dd79f7efe2046ca/aHR0cDovLzU5ZGM2ZDhiNTJiNTkudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxNy8xMC81NjQ1MzQ5YjBkMjQzOWRlODFjZWE1YTc0OTVkN2M0YS5wbmc_p_p100_p_3D.png" alt="">
				</div>
				<nav class="max_nav">
					<ul>
						<li v-for="i in nav"><router-link :to="i.path">{{i.name}}</router-link></li>
					</ul>
				</nav>
				<div class="max_search">
					<input type="text" placeholder="输入关键字"><span class="iconfont icon-search"></span>
				</div>
			</div>
		</div>
		<!-- 大屏悬浮导航 -->
		<div class="max_hover_nav">
			<ul>
				<li v-for="(i, $i) in nav" @click="navs($i)" :class="num==$i?'active':''"><router-link :to="i.path">{{i.name}}</router-link></li>
			</ul>
		</div>
		<!-- ipad/手机导航 -->
		<div class="min_header">
			<div class="min_logo">
				<img src="https://ccdn.goodq.top/caches/1a46466ba86176679dd79f7efe2046ca/aHR0cDovLzU5ZGM2ZDhiNTJiNTkudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxNy8xMC81NjQ1MzQ5YjBkMjQzOWRlODFjZWE1YTc0OTVkN2M0YS5wbmc_p_p100_p_3D.png" alt="">
			</div>
			<div class="min_nav">
				<div class="show" @click="btn">{{show}}</div>
				<ul class="min_nav_list" v-show="type">
					<li v-for="(i, $i) in nav" @click="navs($i)" :class="num==$i?'active':''"><router-link :to="i.path">{{i.name}}</router-link></li>
					<li @click="no()"><router-link to="/logon">登录/注册</router-link></li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				nav: [
					{name: '首页', path: '/'},
					{name: '所有商品', path: '/all'},
					{name: '商城动态', path: '/dynamic'},
					{name: '关于我们', path: '/aboutUs'},
					{name: '线下门店', path: '/site'}
				],
				num: 0,
				show: '首页',
				click: '',
				type: false
			};
		},
		methods: {
			btn() {
				this.type = !this.type;
				if(this.show != '关闭'){
					this.click = this.show;
					this.show = '关闭';
				}else{
					this.show = this.click;
				}
			},
			navs(i){
				this.num = i;
				this.type = !this.type;
				this.show = this.nav[i].name;
			},
			no(){
				this.type = !this.type;
				this.show = '登录/注册';
			}
		}
	}
</script>

<style>
	/* 大屏头部 */
	.max_header{
		width: 100%;
		height: auto;
		background: #757575;
	}
	.max_header_top{
		height: 46px;
		line-height: 46px;
		font-size: 12px;
		color: #ddd;
		margin: 0 36px;
		padding: 0 40px;
		display: flex;
		justify-content: space-between;
	}
	.max_header_data span{
		font-size: 12px;
	}
	.max_header_data span:nth-of-type(2){
		margin-left: 38px;
	}
	.login span{
		margin: 0 5px;
		cursor: pointer;
	}
	.login a{
		color: #ddd;
	}
	.login span:nth-of-type(3){
		font-size: 18px;
		font-weight: 500;
	}
	/* 大屏导航 */
	.max_nav_box{
		width: 100%;
		height: auto;
		display: flex;
		justify-content: space-between;
		background: #fff;
		padding: 10px 76px;
		overflow: hidden;
	}
	.max_logo{
		width: 20%;
		height: auto;
		padding: 20px 0;
	}
	.max_logo img{
		width: 213px;
		margin-left: -10px;
	}
	.max_nav{
		width: 55%;
		padding: 20px 5px;
	}
	.max_nav ul{
		display: flex;
		justify-content: space-between;
		padding: 0 50px 0 50px;
	}
	.max_nav ul li{
		line-height: 48px;
		font-size: 14px;
	}
	.max_nav ul li a{
		color: #777777;
	}
	.max_nav ul li:nth-of-type(1){
		margin-left: 0;
	}
	.max_search{
		width: 25%;
		padding: 20px 0;
		position: relative;
	}
	.max_search input{
		width: 250px;
		height: 37px;
		border: 2px solid rgba(221,51,51,1);
		padding: 10px;
		line-height: 37px;
		position: absolute;
		top: 50%;
		margin-top: -13px;
		left: 0;
	}
	.max_search span{
		display: inline-block;
		background: #dd3333;
		color: #fff;
		height: 37px;
		line-height: 35px;
		padding: 0 15px;
		font-size: 18px;
		position: absolute;
		top: 50%;
		margin-top: -13px;
		left: 250px;
		cursor: pointer;
	}
	/* 大屏悬浮导航 */
	.max_hover_nav{
		width: 100%;
		height: 60px;
		line-height: 60px;
		text-align: center;
		background: #fff;
		box-shadow: 0 1px 2px 1px #f9f9f9;
		position: fixed;
		top: -80px;
		left: 0;
		transition: all .5s;
		z-index: 999;
	}
	.max_hover_nav ul{
		display: flex;
		justify-content: center;
	}
	.max_hover_nav ul li{
		line-height: 60px;
		font-size: 14px;
		margin: 0 30px;
	}
	.max_hover_nav ul li a{
		display: block;
		color: #777777;
		width: 100%;
		height: 100%;
	}
	.max_hover_nav ul li:nth-of-type(1){
		margin-left: 0;
	}	
	/* ipad/手机导航 */
	.min_header{
		width: 100%;
		height: auto;
		display: none;
		z-index: 999;
	}
	.min_logo{
		width: 100%;
		height: auto;
		text-align: center;
		padding: 10px 0;
	}
	.min_logo img{
		width: 236px;
		height: 50px;
	}
	.min_nav{
		width: 100%;
		height: 44px;
		line-height: 44px;
		text-align: center;
		color: #fff;
		font-size: 14px;
	}
	.show{
		background: #565656;
	}
	.min_nav_list{
		width: 100%;
		position: relative;
		z-index: 999;
	}
	.min_nav_list li{
		width: 100%;
		background: #565656;
		border-bottom: 1px #f4f7f7 solid;
	}
	.min_nav_list li a{
		width: 100%;
		height: 100%;
		color: #fff;
		display: block;
	}
	
	@media only screen and (min-width: 764px) and (max-width: 992px){
		.max_header,.max_hover_nav{
			display: none;
		}
		.min_header{
			display: block;
		}
		
	}
	@media only screen and (min-width: 0px) and (max-width: 764px){
		.max_header,.max_hover_nav{
			display: none;
		}
		.min_header{
			display: block;
		}
		.min_nav_list li{
			width: 75%;
			margin: 0 auto;
		}
		
	}
</style>



