<template>
	<div id="app">
		<headers></headers>
		<router-view></router-view>
		<div class="hover_right">
			<span class="iconfont icon-phone03"></span>
			<span class="iconfont icon-qq"></span>
			<span class="iconfont icon-totop" @click="btn"></span>
		</div>
		<footers></footers>
	</div>
</template>

<script>
	import headers from './components/header.vue';
	import footers from './components/footer.vue';
	export default {
		components: {
			headers,
			footers
		},
		name: 'app',
		data() {
			return {
				
			};
		},
		mounted () {
			window.addEventListener('scroll', this.scroll)
		},
		methods: {
			scroll() {
				var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				var hoverNav = document.querySelector('.max_hover_nav');
				var btn = document.querySelector('.btn');
				if(scrollTop > 140){
					hoverNav.style.top = 0 + 'px';
				}else{
					hoverNav.style.top = - 80 + 'px';
				}
			},
			btn(){
				document.documentElement.scrollTop = 0;
				document.body.scrollTop = 0;
			}
		},
		watch: {
			$route(to, from){
				document.documentElement.scrollTop = 0;
				document.body.scrollTop = 0;
			}
		}
	};
</script>

<style>
	*{
		margin: 0;
		padding: 0;
		list-style-type: none;
		text-decoration: none;
		box-sizing: border-box;
		outline: none;
	}
	.hover_right{
		width: 54px;
		height: auto;
		text-align: center;
		position: fixed;
		top: 35%;
		right: 0;
		background: #000;
		padding: 10px 0;
	}
	.hover_right span{
		display: inline-block;
		width: 100%;
		font-size: 30px;
		color: #fff;
		margin: 6px 0;
		cursor: pointer;
	}
	.crumbs{
		width: 100%;
		line-height: 59px;
		background: #dd3333;
		padding: 0 80px;
		color: #fff;
		font-size: 14px;
	}
	.crumbs a{
		color: #fff;
	}
	.crumbs span{
		margin: 0 5px;
	}
	
	@media only screen and (min-width: 764px) and (max-width: 992px){
		.crumbs{
			padding: 0 20px;
		}
		.hover_right{
			display: none;
		}
	}
	
	@media only screen and (min-width: 0px) and (max-width: 764px){
		.crumbs{
			padding: 0 20px;
		}
		.hover_right{
			display: none;
		}
	}
</style>



