import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

var state = {
	// 商品详情
	shop_arr: {},
	// 购物车
	shopping_arr: [],
	// 类型数据
	data_arr: []
}

var getters = {
	// return 正在查看
	get_shop_arr(state){
		return state.shop_arr;
	},
	// return 购物车
	get_shopping_arr(state){
		return state.shopping_arr;
	},
	// 总价
	get_arr_price(state){
		var num = 0;
		for(var i = 0; i < state.shopping_arr.length; i++){
			num += state.shopping_arr[i].price * state.shopping_arr[i].num;
		}
		return num;
	},
	// 购物车是否有数据
	get_type(state){
		var types = false;
		if(state.shopping_arr.length != 0){
			types = true;
		}else{
			types = false;
		}
		return types;
	},
	// 类型数据
	get_data_arr(state){
		return state.data_arr;
	}
}

var mutations = {
	// 正在查看数据
	set_shop_arr(state,json){
		state.shop_arr = json;
	},
	// 获取购物车所有数据
	set_shopping_arr(state, json){
		var types = true;
		var newJson = {};
		newJson = json;
		for(var i = 0; i < state.shopping_arr.length; i++){
			if(state.shopping_arr[i].uid == newJson.uid){
				state.shopping_arr[i].num += newJson.num;
				types = false;
			}
		}
		if(types){
			state.shopping_arr.push(newJson);
		}
	},
	// 数量加
	set_plus(state, uid){
		for(var i = 0; i < state.shopping_arr.length; i++){
			if(state.shopping_arr[i].uid == uid){
				state.shopping_arr[i].num++;
			}
		}
	},
	// 数量减
	set_less(state, uid){
		for(var i = 0; i < state.shopping_arr.length; i++){
			if(state.shopping_arr[i].uid == uid){
				state.shopping_arr[i].num--;
				if(state.shopping_arr[i].num <= 1){
					state.shopping_arr[i].num = 1;
				}
			}
		}
	},
	// 删除数据
	delete_arr(state, uid){
		var types = false;
		var n = 0;
		for(var i = 0; i < state.shopping_arr.length; i++){
			if(state.shopping_arr[i].uid == uid){
				n = i;
				types = true;
			}
		}
		if(types){
			state.shopping_arr.splice(n, 1)
		}
	},
	// 类型数据
	set_son(state, data){
		state.data_arr = data;
	}
}

var actions = {
	// 托管获取前台数据
	get_date({commit}, data){
		commit('set_shop_arr',data);
	},
	// 托管获取加入购物车数据
	get_shopping_data({commit}, data){
		commit('set_shopping_arr', data);
	},
	// 托管数量加
	get_plus({commit}, uid){
		commit('set_plus', uid);
	},
	// 托管数量减
	get_less({commit}, uid){
		commit('set_less', uid);
	},
	// 托管删除数据
	get_delete({commit}, uid){
		commit('delete_arr', uid);
	},
	// 详情数据
	get_data_son({commit},data){
		commit('set_son', data);
	}
}

export default new Vuex.Store({
	state,
	getters,
	mutations,
	actions
})


