		font-family: '微软雅黑';
		font-size: 22px;
		color: #333333;
		padding-left: 10px;
		vertical-align: bottom;
		border-left: 4px solid #dd3333;
		font-weight: 400;
	}
	.all_title h4 span{
		font-family: '微软雅黑';
		font-size: 13px;
		color: #a5a5a5;
		margin-left: 10px;
	}
	.all_title h4 a{
		font-family: 'century gothic';
		font-size: 10px;
		color: #666666;
		float: right;
	}
	.all_box{
		width: 100%;
		height: auto;
		display: flex;
		flex-wrap: wrap;
	}
	.all_box ul{
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-content: space-between;
	}
	.all_box ul li{
		width: 31.5%;
		background: #fff;
		text-align: center;
		padding: 10px 0 32px;
		margin-top: 25px;
		font-family: '微软雅黑';
		transition: all .5s;
	}
	.all_box ul li:nth-child(1),.all_box ul li:nth-child(2),.all_box ul li:nth-child(3){
		margin-top: 0;
	}
	.com_img{
		height: 70%;
	}
	.all_box ul li img{
		width: 70%;
	}
	.all_box ul li h6{
		font-size: 15px;
		color: #191919;
		line-height: 21px;
		font-weight: 500;
		margin-top: 10px;
	}
	.all_box ul li p{
		font-size: 12px;
		color: #a0a0a0;
		margin: 10px 0 20px;
	}
	.all_box ul li span{
		color: #dd3333;
	}	
	.all_box ul li:hover{
		transform: translateY(-10px);
		box-shadow: 0 1px 3px 3px #e5e5e5;
	}
	
	@media only screen and (min-width: 764px) and (max-width: 992px){
		.all{
			padding: 50px 20px;
		}
		.all_box{
			width: 100%;
			padding: 0 10px;
		}
		.all_title h4{
			font-size: 18px;
		}
		.all_title h4 span{
			font-size: 12px;
		}
		.all_title h4 a{
			display: none;
		}
		.all_box ul li{
			width: 49%;
		}
		.all_box ul li{
			margin-top: 20px !important;
		}
		.all_box ul li:hover{
			transform: translateY(0px);
			box-shadow: 0 0px 3px 3px #fff;
		}
	}
	
	
	@media only screen and (min-width: 0px) and (max-width: 764px){
		.all{
			padding: 50px 15px;
		}
		.all_box{
			width: 100%;
			padding: 0 10px;
		}
		.all_title h4{
			font-size: 18px;
		}
		.all_title h4 span{
			font-size: 12px;
		}
		.all_title h4 a{
			display: none;
		}
		.all_box ul li{
			width: 49%;
		}
		.all_box ul li{
			margin-top: 20px !important;
		}
		.all_box ul li:hover{
			transform: translateY(0px);
			box-shadow: 0 0px 3px 3px #fff;
		}
	}
</style>



