<style>
	.index_box{
		width: 100%;
		height: auto;
	}
	.banner{
		background-image: url('https://ccdn.goodq.top/caches/1a46466ba86176679dd79f7efe2046ca/aHR0cDovLzU5ZGM2ZDhiNTJiNTkudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxNy8xMC9iZTFiNjcyOGU5ZjBjN2I0YWE2ZTc4NGU0Mzk4ODdmYS5qcGc_p_p100_p_3D.jpg');
		background-repeat: no-repeat;
	}
	.banner_nav{
		width: 250px;
		margin-left: 80px;
		background: #252525;
		color: #ccc;
	}
	.banner_nav li{
		line-height: 58px;
		border-top: 1px solid #3a3a3a;
		font-size: 14px;
		padding: 0 40px;
		cursor: pointer;
	}
	.banner_nav li:nth-of-type(1){
		border-top: none;
	}
	.banner_nav li:hover{
		background: #404040;
		color: #fff;
	}
	.banner_nav li a{
		display: block;
		width: 100%;
		height: auto;
		color: #ccc;
	}
	#row-1{
		background: #f2f2f2;
		padding: 50px 80px;
	}
	.overlay_bg{
		width: 100%;
		height: auto;
		display: flex;
		justify-content: space-between;
	}
	.background-overlay{
		width: 32.7%;
		position: relative;
	}
	.img_bg{
		width: 100%;
		position: relative;
		top: 0;
		left: 0;
	}
	.img_bg img{
		width: 100%;
		max-height: 200px;
	}
	.overlay_text{
		width: 100%;
		height: 100%;
		padding-left: 50%;
		position: absolute;
		top: 0;
		left: 0;
	}
	.overlay_text h6{
		font-size: 16px;
		font-weight: 700;
		margin: 50px 0 20px;
	}
	.overlay_text span{
		font-size: 13px;
		padding-right: 20px;
	}
	#commodity{
		width: 100%;
		height: auto;
		background: #f2f2f2;
	}
	#news{
		width: 100%;
		height: auto;
		padding: 80px 80px 30px;
	}
	.news_title{
		width: 100%;
		height: auto;
	}
	.news_title h4{
		font-family: '微软雅黑';
		font-size: 22px;
		color: #333333;
		padding-left: 10px;
		vertical-align: bottom;
		border-left: 4px solid #333;
		font-weight: 400;
	}
	.news_title h4 span{
		font-family: '微软雅黑';
		font-size: 13px;
		color: #a5a5a5;
		padding: 0 10px 0 0;
		margin-left: 10px;
	}
	.news_title h4 a{
		font-family: 'century gothic';
		font-size: 10px;
		color: #666666;
		float: right;
	}
	.news_list{
		width: 100%;
		height: auto;
		margin-top: 20px;
	}
	.news_list ul{
		width: 100%;
		height: auto;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	.news_list ul li{
		width: 23%;
		background: rgba(102,102,102,1);
	}
	.news_list ul li:hover{
		background: rgba(51,51,51,1);
	}
	.news_img{
		width: 100%;
		height: auto;
		overflow: hidden;
	}
	.news_list ul li:hover img{
		transform: scale(1.1);
	}
	.news_list ul li img{
		width: 100%;
		transition: all .7s;
	}
	.news_text{
		width: 100%;
		height: 100px;
		padding: 0 10px;
		display: -webkit-box;
		text-overflow: ellipsis;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 1;
	}
	.news_text p:nth-of-type(1){
		color: #fff;
		font-size: 14px;
		overflow: hidden;
	}
	.news_text p:nth-of-type(2){
		overflow: hidden;
		font-size: 12px;
		color: #999;
		margin-top: 10px;
	}
	.exemption{
		width: 100%;
		height: auto;
		margin-top: 50px;
	}
	.exemption ul{
		width: 100%;
		height: auto;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	.exemption ul li{
		width: 23%;
		height: auto;
		display: flex;
	}
	.exe_img img{
		width: 70px;
	}
	.exe_text p{
		font-size: 16px;
		color: #494949;
		margin: 10px 0 0;
	}
	.exe_text span{
		font-size: 12px;
		color: #bababa;
	}
	
	
	
	
	@media only screen and (min-width: 764px) and (max-width: 992px){
		#news{
			padding: 50px 20px;
		}
		.news_list{
			padding: 0 20px;
		}
		.banner,#row-1{
			display: none;
		}
		.news_list ul li{
			width: 100%;
			background: rgba(51,51,51,1) !important;
			margin: 30px 0;
		}
		.exemption ul li{
			width: 100%;
		}
		.news_list ul li:hover img{
			transform: scale(1);
		}
	}
	
	@media only screen and (min-width: 0px) and (max-width: 764px){
		#news{
			padding: 50px 20px;
		}
		.news_list{
			padding: 0 10px;
		}
		.banner,#row-1{
			display: none;
		}
		.news_list ul li{
			width: 100%;
			background: rgba(51,51,51,1) !important;
			margin: 30px 0;
			border: none;
		}
		.exemption ul li{
			width: 100%;
		}
		.news_list ul li:hover img{
			transform: scale(1);
		}
	}
	
</style>



